/*Pixels to rems*/
.wrapper {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 62.5em) {
  .wrapper {
    grid-template-columns: 1fr 1fr;
    gap: 120px;
  }
}

body {
  background-image: url("/images/bg-mobile.svg");
  background-size: cover;
  background-color: var(--Violet);
}
@media (min-width: 62.5em) {
  body {
    background-image: url("/images/bg-desktop.svg");
  }
}

@media (min-width: 62.5em) {
  .header {
    grid-column: span 2;
  }
}
.header__image {
  margin-top: 4.6875rem;
  margin-left: 5rem;
  margin-bottom: 8.75rem;
}
@media (min-width: 62.5em) {
  .header__image {
    margin-top: 4rem;
    margin-left: 5rem;
    margin-bottom: 6.5625rem;
  }
}

.main__image {
  width: 37.125rem;
  height: 26.25rem;
  margin: 0 4.625rem 9.375rem 5rem;
}
@media (min-width: 62.5em) {
  .main__image {
    width: 43.625rem;
    height: 31rem;
    margin: 0 4.625rem 9.375rem 5rem;
    grid-column: 1/2;
  }
}

.section {
  text-align: center;
  font-family: "Open Sans", sans-serif;
}
@media (min-width: 62.5em) {
  .section {
    text-align: left;
  }
}
.section__headline {
  font-family: "Poppins", sans-serif;
  font-size: 2.375rem;
  color: #fff;
  font-weight: 600;
  margin: 0 4.625rem 0 5rem;
}
.section__headline:nth-child(2) {
  margin-bottom: 2.625rem;
}
@media (min-width: 62.5em) {
  .section__headline {
    margin: 0 4.625rem 0.9375rem 0;
  }
  .section__headline:nth-child(2) {
    margin-bottom: 2.8125rem;
  }
}
.section__text {
  color: #fff;
  font-size: 1.625rem;
  margin: 0 4.625rem 3.75rem 5rem;
}
@media (min-width: 62.5em) {
  .section__text {
    margin: 0 4.625rem 0 0;
  }
}

.btn {
  width: 25rem;
  height: 5rem;
  border: 1px solid var(--Violet);
  background: #fff;
  color: var(--Violet);
  font-size: 1.375rem;
  border-radius: 50px;
  letter-spacing: 1px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.25);
  margin-bottom: 8rem;
}
.btn:hover {
  cursor: pointer;
  background: var(--Soft-Magenta);
  color: #fff;
}
@media (min-width: 62.5em) {
  .btn {
    margin-top: 1.9375rem;
  }
}

@media (min-width: 62.5em) {
  .footer {
    grid-column: span 2;
    justify-self: end;
    margin-bottom: 50px;
    margin-right: 50px;
  }
}
.footer__icons {
  color: #fff;
  text-align: center;
}
.footer__icons i {
  font-size: 3.625rem;
  margin: 0 0.6875rem;
}
.footer__icons i:hover {
  cursor: pointer;
  color: var(--Soft-Magenta);
}

:root {
  --Violet: hsl(257, 40%, 49%);
  --Soft-Magenta: hsl(300, 69%, 71%);
}

/* Box sizing rules */
html {
  box-sizing: inherit;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}